import apiCall from "../apiCall";
import Store from "../Store";
import AuthService from "./auth.service";
import { successToaster } from "./notify.service";
import $ from "jquery";
 const ChatService={
  Get_Requests(status="all")
{
    apiCall.get('chat/requests?status='+status).then(res=>{
     
        Store.dispatch({type:"Set_Chat_Requests",payload:res.data.data})
    })
},
MessageToBot(form)
{
    
    apiCall.post("chat/MessageToBot",form).then(res=>{
     ChatService.Get_Messages(localStorage.getItem("chatWithID"))
    })
},
Get_Messages(id)
{
    apiCall.get('chat/messages/'+id).then(res=>{
     
        Store.dispatch({type:"Set_Chat_Messages",payload:res.data.data})
        ChatService.Get_BotUser(id)
        // setTimeout(() => {
            $('#chating').animate({scrollTop: 10000000000000000000}, "slow");  
        //   }, 1000);
    })
},
Get_BotUser(id)
{
    apiCall.get('chat/botuser/'+id).then(res=>{
     
        Store.dispatch({type:"Set_Bot_User",payload:res.data.data})
    })
},


}

export default ChatService;