
const initState = {
    loading: false,
    myUsers:[],
    tempData:{
        type:"none",
        data:null
    },
    myContacts:[],
    articleCategories:[],
    articles:[],
    Article:{
        title:"",
        content:""
    },
    BotUser:{
        name:"",
        is_online:"0"
    },
    Chat_Requests:[],
    Chat_Messages:[],
    campaigns:[],
    etemplates:[],
}
const DefaultReducer = (state = initState, action) => {
    if (action.type === "Set_Loader") {
        return { ...state, loading: true }
    }
    else if (action.type === "Set_myUsers") {
        return { ...state, myUsers: action.payload }
    }
    else if (action.type === "Set_myContacts") {
        return { ...state, myContacts: action.payload }
    }
    else if (action.type === "Set_tempData") {
        return { ...state, tempData: action.payload }
    }
    else if (action.type === "Set_Articles") {
        return { ...state, articles: action.payload }
    }
    else if (action.type === "Set_ArticleCategories") {
        return { ...state, articleCategories: action.payload }
    }
    else if (action.type === "Set_Article") {
        return { ...state, Article: action.payload }
    }
    else if (action.type === "Set_Chat_Requests") {
        return { ...state, Chat_Requests: action.payload }
    }
    else if (action.type === "Set_Chat_Messages") {
        return { ...state, Chat_Messages: action.payload }
    }
    else if (action.type === "Set_ETemplates") {
        return { ...state, etemplates: action.payload }
    }
    else if (action.type === "Set_Campaigns") {
    
        console.log(action.payload);
        return { ...state, campaigns: action.payload }
    }
    else if (action.type === "Set_Bot_User") {
        return { ...state, BotUser: action.payload }
    }
    else
    {
    return state;
    }

} 

export default DefaultReducer;