import apiCall from "../apiCall";
import Store from "../Store";
import AuthService from "./auth.service";
import { successToaster } from "./notify.service";

 const ArticleService={
  Get_Articles(status="all")
{
    apiCall.get('articles/list?status='+status).then(res=>{
        Store.dispatch({type:"Set_Articles",payload:res.data})
    })
},
Get_ArticleCategories()
{
    apiCall.get('articlesCategory/list').then(res=>{
        console.log('Cates',res.data);
        
        Store.dispatch({type:"Set_ArticleCategories",payload:res.data})
    })
},
Add_Category(form)
{
    return apiCall.post('articlesCategory/add',form);
},
Add_Article(form)
{
    return apiCall.post('articles/add',form);
}

}

export default ArticleService;