import axios from "axios";
import { warnToaster } from "./services/notify.service";
import UserService from "./services/user.service";


// export default myaxios;
const token = localStorage.getItem("access_token")
var apiCall = axios.create({baseURL:"https://infinitybot.io/Backend/api/"})


        apiCall.interceptors.request.use(config => {
            config.params = {
             token:token,
             // spread the request's params
              ...config.params,
            };
            return config;
          });

          apiCall.interceptors.response.use(function (response) {
            return response;
        }, function (error) {
            if (401 === error.response.status) {
               warnToaster("Session has been Expired")
               UserService.Logout_User()
            } else {
                return Promise.reject(error);
            }
        });


export default apiCall;