import { useState, useEffect, useRef } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Picker from "emoji-picker-react";
import { FaRegSmile } from "react-icons/fa";
import $ from "jquery";
import Text from "@/components/Typography/Text";

import {
  AttachmentIcon,
  CallIcon,
  CameraIcon,
  MenuIcon,
  SearchIcon,
  SendIcon,
  SilentIcon,
  VoiceIcon,
} from "@/components/Icons";

import {
  MessagesContainer,
  InboxContainer,
  InboxCategoryWrapper,
  TextCategoryWrapper,
  InboxesWrapper,
  Inbox,
  TextWrapper,
  InfoMessageWrapper,
  CountOfMessage,
  MessageText,
  MessageWrapper,
  TextMessageWrapper,
  DetailMessage,
  Dot,
  MenuDot,
  MenuWrapper,
  Tool,
  ToolMessageWrapper,
  Profile,
  ProfileBarWrapper,
  BarMessageWrapper,
  ButtonSendMessage,
  InboxMessagesContainer,
  Messages,
  InputMessageWrapper,
  EmojiWrapper,
  InputMessage,
  SendMessageWrapper,
} from "./messages.style";
import ChatService from "@/Store/services/chat";
import { connect } from "react-redux";
import SoundService from "@/Store/services/Sound";

  function Message(props) {
  const navigate = useNavigate();
  const [currentParams, setCurrentParams] = useState("all");
  const [currentFocusMessageId, setCurrentFocusMessageId] = useState(1);

  const [typing, setTyping] = useState(false); // when typing input message
  const [currentWidth, setCurrentWidth] = useState(null);
  const [clickedInbox, setClickedInbox] = useState(false); // when someone has'nt click inbox (for mobile)
  const [showEmoji, setShowEmoji] = useState(false);
  const params = useParams();
  const [User, setUser] = useState(JSON.parse(localStorage.getItem("user")));
  const inputMessageRef = useRef();
  const onSelectImage=(event)=> {



    var tmppath = URL.createObjectURL(event.target.files[0]);
    // $("#AddEmpImage").fadeIn("fast").attr('src',tmppath);
   var SelectedImage=event.target.files[0];
    console.log(SelectedImage.size)
if(SelectedImage.size>5000000)
{
alert("Sorry Max File size is 5 MB")
return;
}
    var formData = new FormData()
    formData.append("message",$("#imageToUpload").val())
    formData.append("chat_request",localStorage.getItem("chatWithID"))
    formData.append("type","file")
    formData.append("image",SelectedImage)
    ChatService.MessageToBot(formData)
    $("#messageInput").html("")
    $("#imageToUpload").val("")
}
const SendMessageWithFile=()=>{
  $("#imageToUpload").click()
  // var input = $(document.createElement("input"));
  // input.attr("type", "file");
  // // add onchange handler if you wish to get the file :)
  // input.trigger("click"); // opening dialog
}
  const SendMessage=()=>{
    if($("#messageInput").html()=="")
    {
      return;
    }
 
    var form=new FormData();
    form.append("message",$("#messageInput").html())
    form.append("chat_request",localStorage.getItem("chatWithID"))
   ChatService.MessageToBot(form)
   $("#messageInput").html("")
  }
  useEffect(() => {
    $(window).on('keydown', function(e) {

      if (e.which == 13) {

      
          // typingMessage();

          SendMessage();
          return false;
      }
  });
  
    if(localStorage.getItem("chatWithID"))
    {

      setCurrentFocusMessageId(localStorage.getItem("chatWithID"))
      ChatService.Get_Messages(localStorage.getItem("chatWithID"))
     

    }
    ChatService.Get_Requests();
    const currentWidth = window.innerWidth;
    setCurrentWidth(currentWidth);

    window.addEventListener("resize", (e) => {
      const width = e.target.innerWidth;
      setCurrentWidth(width);
    });
  }, []);

  const handleClickTextCategory = (e, category) => {
    e.preventDefault();
    navigate(`?category=${category}`);

    ChatService.Get_Requests(category)
    setCurrentParams(category);
    
    
  };

  const handleChangeFocusMessageId = (id) => {
    
    setCurrentFocusMessageId(id);
    ChatService.Get_Messages(localStorage.getItem("chatWithID"))

    if (currentWidth <= 996) {
      setClickedInbox(true);
      navigate(`${id}`);
    }
  };

  const handleMaxLengthInputMessage = (e) => {
    setTyping(true);

    const currentTextLength = Number(e.target.outerText.length);
    const maxLength = Number(e.target.dataset.maxLength);

    if (currentTextLength > maxLength && e.key !== "Backspace") {
      e.preventDefault();
    }
  };

  const handleKeyUpInput = (e) => {
    const currentTextLength = Number(e.target.outerText.length);

    if (currentTextLength <= 0) {
      setTyping(false);
    }
    
  };
 
  const handleClickEmoji = (e, emojiObject) => {
    inputMessageRef.current.innerText += emojiObject.emoji;
    setTyping(true);
  };

  return (
    <MessagesContainer>

      {(!params.id || currentWidth >= 996) && (
        <InboxContainer>
          <InboxCategoryWrapper>
            <TextCategoryWrapper
              onClick={(e) => handleClickTextCategory(e, "all")}
            >
              <Text
                size="sm"
                weight="medium"
                className={currentParams === "all" && "active"}
              >
                All
               


              </Text>
            </TextCategoryWrapper>
        
            <TextCategoryWrapper
              onClick={(e) => handleClickTextCategory(e, "unread")}
            >
              <Text
                size="sm"
                weight="medium"
                className={currentParams === "unread" && "active"}
              >
                Unread
              </Text>
            </TextCategoryWrapper>
            <TextCategoryWrapper
              onClick={(e) => handleClickTextCategory(e, "unresolved")}
            >
              <Text
                size="sm"
                weight="medium"
                className={currentParams === "unresolved" && "active"}
              >
                Unresolved
              </Text>
            </TextCategoryWrapper>
            <TextCategoryWrapper
              onClick={(e) => handleClickTextCategory(e, "filters")}
            >
              <Text
                size="sm"
                weight="medium"
                className={currentParams === "filters" && "active"}
              >
                Filters
              </Text>
            </TextCategoryWrapper>
          </InboxCategoryWrapper>
         
          <InboxesWrapper>
          <input style={{"display":"none"}} id="imageToUpload" onChange={(event)=>{onSelectImage(event)}} type="file"/>
            {
              props.requests.map((item)=>{
                return (     <Inbox 
                  className={currentFocusMessageId === item.id ? "active" : ""}
                  onClick={() =>
                    {
                      localStorage.setItem("chatWithID",item.id)
                    handleChangeFocusMessageId(item.id)
                  }
                  }
                >
                  <img
                    src="/myicons/avartar.png"
                    alt="avatar"
                    className="avatar-message"
                  />
                  <TextWrapper>
                    <Text
                      weight="semi-bold"
                      size="sm"
                      className="name-from-message"
                    >
                      {item.user.name}
                    </Text>
                    <Text size="sm" className="piece-message">
                    {item.message}
                    </Text>
                  </TextWrapper>
                  <InfoMessageWrapper>
                    <Text size="xs">{item.readable_time}</Text>
                    {item.count>0?<CountOfMessage className="count-message">{item.count}</CountOfMessage>:''}
                  </InfoMessageWrapper>
                </Inbox>)
              })
            }
       
          </InboxesWrapper>
        </InboxContainer>
      )}
      {((currentWidth <= 996 && clickedInbox && params.id) ||
        currentWidth >= 996) && (
        <InboxMessagesContainer>
          <BarMessageWrapper>
            <ProfileBarWrapper>
              <img src="/myicons/defaultuser.png" alt="avatar" className="avatar" />
              <Profile status="online">
                <Text weight="semi-bold">{props.botUser.name}</Text>
                <Text size="xs" className="status">
                 {props.botUser.is_online=="1"? "Online":"offline"}
                </Text>
              </Profile>
            </ProfileBarWrapper>
            <ToolMessageWrapper>
              <Tool>
                <SilentIcon />
                <CameraIcon />
                <CallIcon />
                <MenuIcon />
              </Tool>
              <MenuWrapper>
                <SearchIcon />
                <MenuDot>
                  <Dot />
                  <Dot />
                  <Dot />
                </MenuDot>
              </MenuWrapper>
            </ToolMessageWrapper>
          </BarMessageWrapper>

          <Messages id="chating">
            {
              
              props.messages.map((item)=>{
                return (
<>
            {item.sender!=User.id && <MessageWrapper>
              <img src="/myicons/defaultuser.png" alt="avatar" className="avatar" />
              <DetailMessage>
                <TextMessageWrapper className="message-wrapper">
                  <Text size="xs" weight="semi-bold">
                    {item.botuser.name}
                  </Text>
                  <Text className="time">3:20 PM</Text>
                </TextMessageWrapper>
                <MessageText className="message-text">
                <div dangerouslySetInnerHTML={{__html:item.message}} >
                </div>
                </MessageText>
              </DetailMessage>
            </MessageWrapper>}

            {item.sender==User.id &&   <MessageWrapper mine>
              <img src="/myicons/bot.jpg" alt="avatar" className="avatar" />
              <DetailMessage>
                <TextMessageWrapper className="message-wrapper">
                  <Text size="xs" weight="semi-bold" className="name">
                    {User.name}
                  </Text>
                  <Text className="time">3:20 PM</Text>
                </TextMessageWrapper>
                <MessageText className="message-text">
                   <div dangerouslySetInnerHTML={{__html:item.message}} >
                </div>
                </MessageText>
              </DetailMessage>
            </MessageWrapper>}

</>
                )
              })
            }
      
          </Messages>

          <InputMessageWrapper>
   
            {showEmoji && (
              <EmojiWrapper
                onMouseMove={() => setShowEmoji(true)}
                onMouseLeave={() => setShowEmoji(false)}
              >
                <Picker disableSkinTonePicker onEmojiClick={handleClickEmoji} />
              </EmojiWrapper>
            )}
            <FaRegSmile
              className="emoji-icon"
              color="#AFAEB1"
              onMouseMove={() => setShowEmoji(true)}
            />
            <InputMessage
            id="messageInput"
              typing={typing}
              contentEditable
              placeholder="Your messages"
              data-max-length={250}
              onKeyDown={handleMaxLengthInputMessage}
              onKeyUp={handleKeyUpInput}
              ref={inputMessageRef}
            />
                  <SendMessageWrapper>
              <VoiceIcon />
              <div onClick={()=>{SendMessageWithFile()}}>
              <AttachmentIcon />
             </div>
              <ButtonSendMessage>
                <div onClick={()=>{SendMessage()}}>
                <SendIcon  />
                </div>
               
              </ButtonSendMessage>
            </SendMessageWrapper>
          </InputMessageWrapper>
        </InboxMessagesContainer>
      )}
    </MessagesContainer>
  );
}

function mapStateToProps(state) {
console.log("Chat Requests",state.Chat_Requests);
 
  return {
  requests:state.Chat_Requests,
  messages:state.Chat_Messages,
  botUser:state.BotUser
  };
}
export default connect(mapStateToProps, {})(Message);
